.header-btn {
	display: inline-block;
	width: 2rem;
	height: 2rem;
	line-height: 2rem;
	text-align: center;
	font-size: 1rem;
}
.center-block{
	margin-right: auto;
    margin-left: auto;
    float: inherit !important;
}
.header-btn.has-new {
	position: relative;
}
.header-btn.has-new:before {
	content: '';
	width: 6px;
	height: 6px;
	background: #c21a1a;
	position: absolute;
	top: 4px;
	right: 4px;
	border-radius: 50%;
}
.header-toggler {
	width: 2rem;
	height: 2rem;
	position: relative;
	color: #fff;
}
.header-toggler:hover {
	color: #fff;
}
.header-toggler-icon {
	position: absolute;
	width: 1rem;
	height: 2px;
	color: inherit;
	background: currentColor;
	border-radius: 3px;
	top: 50%;
	left: 50%;
	margin: -2px 0 0 -.5rem;
	box-shadow: 0 5px currentColor, 0 -5px currentColor;
}
.footer {
    border-top: 1px solid #eaeaea;
    font-size: 0.875rem;
    padding: 1.25rem 0;
    color: #ffffff;
    background: #28606b;
}
.footer .social ul li{
	float:left;
	padding:7px;
}
.footer .social ul li a{
	color:#898989;
}
.footer .social{
	margin:0 auto;
}
.footer a:not(.btn) {
	color: #ecb403;
}
.card .card-body h2{
    font-size: 14px;
}
@media print {
	.footer {
		display: none;
	}
}

