	border-radius: 0 0 3px 3px;
}
.nav-tabs .nav-submenu .nav-item {
	display: block;
	padding: .5rem 1rem;
	color: #9aa0ac;
	margin: 0 !important;
	cursor: pointer;
	transition: .3s background;
}
