/***** time-line*****/
.timeline__item:after{
	background: #fff !important;
}
.timeline--horizontal .timeline-divider {
	background: #5a5de3 !important;
}
.timeline__content {
    background-color: #fff;
    border-radius: 10px;
    display: block;
    padding: 1.25rem;
    position: relative;
}

/*-----Back to Top-----*/

#back-to-top {
	background: #5a5de3;
	color: #fff;
	position: fixed;
	bottom: 20px;
	right: 20px;
	z-index: 99;
	display: none;
	text-align: center;
	border-radius: 5px;
	-moz-border-radius: 5px;
	-webkit-border-radius: 5px;
	-o-border-radius: 2px;
	z-index: 10000;
	height: 50px;
	width: 50px;
	background-repeat: no-repeat;
	background-position: center;
	transition: background-color 0.1s linear;
	-moz-transition: background-color 0.1s linear;
	-webkit-transition: background-color 0.1s linear;
	-o-transition: background-color 0.1s linear;
}
#back-to-top i {
	padding-top: 15px;
	font-size: 16px;
}
#back-to-top:hover {
	background: #fff;
	color: #2EBE9E;
	border: 2px solid #2EBE9E;
}

/*---- Time line -----*/

.timeline {
	position: relative;
	margin: 0 0 2rem;
	padding: 0;
	list-style: none;
}
.timeline:before {
	background-color: #e9ecef;
	position: absolute;
	display: block;
	content: '';
	height: 100%;
	top: 0;
	bottom: 0;
	left: 4px;
}
.timeline-item {
	position: relative;
	display: flex;
	padding-left: 2rem;
	margin: .5rem 0;
}
.timeline-item:first-child:before, .timeline-item:last-child:before {
	content: '';
	position: absolute;
	background: #fff;
	width: 1px;
	left: .25rem;
}
.timeline-item:first-child {
	margin-top: 0;
}
.timeline-item:first-child:before {
	top: 0;
	height: .5rem;
}
.timeline-item:last-child {
	margin-bottom: 0;
}
.timeline-item:last-child:before {
	top: .5rem;
	bottom: 0;
}
.timeline-badge {
	position: absolute;
	display: block;
	width: 0.4375rem;
	height: 0.4375rem;
	left: 1px;
	top: .5rem;
	border-radius: 100%;
	border: 1px solid #fff;
	background: #adb5bd;
}
.timeline-time {
	white-space: nowrap;
	margin-left: auto;
	color: #9aa0ac;
	font-size: 87.5%;
}

